.sec-form {
  background: #eaeaea;
  padding: 5.7692307692vw 0 10vw;
}
.sec-form .base {
  max-width: 80vw;
}
.sec-form .in {
  background: #fff;
  border-radius: 3.8461538462vw;
  margin: 5.3846153846vw 0;
  padding: 5vw 1.5384615385vw 5.3846153846vw;
}
.sec-form .in .list {
  max-width: 54.7692307692vw;
  margin: 0 auto;
}
.sec-form .in .list .box {
  font-size: 1.2307692308vw;
  line-height: 1.6em;
  margin-bottom: 2.3076923077vw;
}
.sec-form .in .list .box:last-child {
  margin-bottom: 0;
}
.sec-form .in .list .box .ttl {
  font-size: 1.2307692308vw;
  font-weight: bold;
  letter-spacing: 0.1em;
  margin-bottom: 0.6923076923vw;
}
.sec-form .in .list .box .ttl span {
  font-size: 0.9230769231vw;
  color: red;
  vertical-align: super;
}
.sec-form .in .list .box .ttl small {
  font-size: 1.0769230769vw;
  font-weight: normal;
  display: block;
  line-height: 1.4;
  margin: 0.2307692308vw 0 0;
}
.sec-form .in .list .box .input input[type=text],
.sec-form .in .list .box .input input[type=email],
.sec-form .in .list .box .input input[type=url],
.sec-form .in .list .box .input input[type=tel],
.sec-form .in .list .box .input select,
.sec-form .in .list .box .input textarea {
  font-size: 1.2307692308vw;
  background: #fff;
  width: 100%;
  border: 1px solid #c6c6c6;
  border-radius: 0.4615384615vw;
  padding: 0.7692307692vw 0.9230769231vw;
  appearance: none;
}
.sec-form .in .list .box .input select {
  padding-top: 0;
  padding-bottom: 0;
  height: 3.3076923077vw;
}
.sec-form .in .list .box .input ::placeholder {
  font-size: 1.1538461538vw;
  color: #c6c6c6;
  letter-spacing: 0.1em;
}
.sec-form .in .list .item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2.3076923077vw;
  width: 100%;
}
.sec-form .in .list .item.col02 li {
  width: 48%;
}
.sec-form .in .list .item.col03 {
  max-width: 34.6153846154vw;
}
.sec-form .in .list .item.col03 li {
  width: 30%;
  display: flex;
  align-items: center;
}
.sec-form .in .list .item.col03 li span {
  vertical-align: middle;
  margin-left: 0.7692307692vw;
}
.sec-form .in .list .check {
  display: flex;
  flex-wrap: wrap;
}
.sec-form .in .list .check li {
  margin: 0 2.3076923077vw 0 0;
}
.sec-form .in .list .check li:last-child {
  margin-right: 0;
}
.sec-form .in .list .check li label {
  cursor: pointer;
}
.sec-form .in .list .check.m02 li {
  margin: 0 1.2307692308vw 0.3846153846vw 0;
}
.sec-form .in .list .file label {
  border: 1px solid #c6c6c6;
  border-radius: 0.4615384615vw;
  padding: 0.5384615385vw 0;
  width: 12.0769230769vw;
  text-align: center;
  display: block;
  cursor: pointer;
}
.sec-form .in .list .file input {
  display: none;
}
.sec-form .in .list .file span {
  font-size: 1.0769230769vw;
  color: #3871e0;
  font-weight: bold;
  padding-left: 1.5384615385vw;
  position: relative;
}
.sec-form .in .list .file span:before {
  content: "";
  background: url(../../img/common/ico_download.svg) no-repeat left top/100%;
  width: 0.9230769231vw;
  height: 0.8461538462vw;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.sec-form .in .list .file .preview {
  font-size: 0.9230769231vw;
}
.sec-form .in button {
  border: none;
  cursor: pointer;
  appearance: none;
}
@media only screen and (max-width: 767px) {
  .sec-form {
    padding: 16vw 0;
  }
  .sec-form .base {
    max-width: 100%;
  }
  .sec-form .in {
    border-radius: 5.3333333333vw;
    margin: 10.1333333333vw 0;
    padding: 12vw 5.3333333333vw 13.3333333333vw;
  }
  .sec-form .in .list {
    max-width: 100%;
  }
  .sec-form .in .list .box {
    font-size: 3.7333333333vw;
    margin-bottom: 5.3333333333vw;
  }
  .sec-form .in .list .box .ttl {
    font-size: 3.7333333333vw;
    margin-bottom: 2.4vw;
  }
  .sec-form .in .list .box .ttl span {
    font-size: 3.2vw;
  }
  .sec-form .in .list .box .ttl small {
    font-size: 3.2vw;
    margin: 0.8vw 0 0;
  }
  .sec-form .in .list .box .input input[type=text],
  .sec-form .in .list .box .input input[type=email],
  .sec-form .in .list .box .input input[type=url],
  .sec-form .in .list .box .input input[type=tel],
  .sec-form .in .list .box .input select,
  .sec-form .in .list .box .input textarea {
    font-size: 4.2666666667vw;
    border-radius: 1.6vw;
    padding: 2.1333333333vw 2.6666666667vw;
  }
  .sec-form .in .list .box .input select {
    height: 11.4666666667vw;
  }
  .sec-form .in .list .box .input ::placeholder {
    font-size: 3.7333333333vw;
  }
  .sec-form .in .list .item {
    margin-bottom: 5.3333333333vw;
  }
  .sec-form .in .list .item.col03 {
    max-width: 100%;
  }
  .sec-form .in .list .item.col03 li span {
    margin-left: 1.8666666667vw;
  }
  .sec-form .in .list .check li {
    margin: 0 4vw 0 0;
  }
  .sec-form .in .list .check.m02 {
    margin-bottom: 2.6666666667vw;
  }
  .sec-form .in .list .check.m02 li {
    margin: 0 0 1.3333333333vw;
    width: 100%;
  }
  .sec-form .in .list .check.m02 li:last-child {
    margin-bottom: 0;
  }
  .sec-form .in .list .file label {
    border-radius: 1.6vw;
    padding: 1.8666666667vw 0;
    width: 41.8666666667vw;
  }
  .sec-form .in .list .file span {
    font-size: 3.2vw;
    padding-left: 5.3333333333vw;
  }
  .sec-form .in .list .file span:before {
    width: 3.2vw;
    height: 2.9333333333vw;
  }
  .sec-form .in .list .file .preview {
    font-size: 3.2vw;
  }
}